.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    padding: 1em;
    margin: 1em;
    border-radius: .6em;
    background-color: rgba(255, 255, 255, 0.185);
}

.card-image {
    position: relative;
    width: 90%;
    margin-bottom: 1em;
    border: 2px solid #56e543;
    border-radius: .6em;
    cursor: pointer;
}

.img1 {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: .6em;
    opacity: 0;
}

.img2 {
    width: 100%;
    height: 100%; 
    border-radius: .6em;
}

.img1:hover {
    opacity: 1;
    transition: opacity .5s ease-in-out;
}

.img2:hover {
    opacity: 0;
    transition: opacity .5s ease-in-out;
}

.card-image i {
    position: absolute;
    bottom: 1em;
    right: 1em;
    filter: drop-shadow(1px 1px 1px #56e543)
}

.card-container p {
    margin-bottom: 1.5em;
}

.card-container button {
    margin: .6em;
}

.card-container button a {
    padding: .8em;
}

@media (min-width: 900px) {
    .card-image {
        width: 75%;
    }
    .card-container p {
        width: 90%;
    }
}